<template>
  <el-table
    ref="table"
    style="overflow:auto;height: calc(100% - 35px);"
    highlight-current-row
    :data="mainData"
    fit
    :row-key="getRowKey"
    class="table-sty"
    :border="border"
    :header-cell-style="headerStyle"
    :cell-class-name="addTdClass"
    @current-change="handleCurrentChange"
    @selection-change="handleSelectionChange"
  >
    <el-table-column v-if="showRadio" width="55" align="center">
      <template slot-scope="scope">
        <el-radio v-model="checkedRadio" :label="scope.row[radioFlag]" class="clearradio" @change="radioChange(scope.row)" />
      </template>
    </el-table-column>
    <el-table-column v-if="showcheckbox" type="selection" :selectable="selectable" width="55" align="center" :reserve-selection="true" />
    <el-table-column v-if="showIndex" type="index" label="序号" width="50" align="center" />
    <template v-for="(item, index) of columns">
      <!-- 顿号分割 没写-->
      <el-table-column v-if="item.division" :key="index" :prop="item.pram" :render-header="renderHeader" :label="item.label" :width="item.width?item.width:'auto'" align="center" :show-overflow-tooltip="!(JSON.stringify(item.tooltip)==='false')">
        <template slot-scope="scope">
          <slot :row="scope.row" :index="scope.$index" :name="item.pram">
            <div v-if="scope.row[item.pram]" class="textAlong">
              <span v-for="(ele, id) in scope.row[item.pram]" :key="ele">
                {{ ele[item.subParam] === "" ? "" : id > 0 ? "、" : ""
                }}{{ item.subParam === "arr" ? ele : ele[item.subParam] }}
              </span>
            </div>
          </slot>
        </template>
      </el-table-column>
      <!-- 跳转详情 -->
      <el-table-column v-else-if="item.openDetail" :key="index" :prop="item.pram" :render-header="renderHeader" :label="item.label" :width="item.width?item.width:'auto'" align="center" :show-overflow-tooltip="!(JSON.stringify(item.tooltip)==='false')">
        <template slot-scope="scope">
          <slot :row="scope.row" :index="scope.$index" :name="item.pram">
            <el-button type="text" @click="openDetail(scope.row)">{{ scope.row[item.pram] }}</el-button>
          </slot>
        </template>
      </el-table-column>
      <!-- 开关模块 -->
      <el-table-column v-else-if="item.switch" :key="index" :prop="item.pram" :render-header="renderHeader" :width="item.width?item.width:'auto'" :label="item.label" align="center" :show-overflow-tooltip="!(JSON.stringify(item.tooltip)==='false')">
        <template slot-scope="scope">
          <slot :row="scope.row" :index="scope.$index" :name="item.pram">
            <el-switch v-model="scope.row[item.pram]" :active-color="item.activeColor?item.activeColor:'#4778E5'" :active-value="item.activeValue?item.activeValue:1" :inactive-value="item.inactiveValue?item.inactiveValue:0" :inactive-color="item.inactiveColor?item.inactiveColor:'#E5E5E5'" :disabled="eval(scope.row,item.disabled)" @change="setStatus(scope.row, scope.row[item.pram])" />
          </slot>
        </template>
      </el-table-column>
      <!-- 图片模块 -->
      <el-table-column v-else-if="item.img" :key="index" prop="img" :render-header="renderHeader" :label="item.label" class="123" :width="item.width?item.width:'auto'" align="center">
        <template slot-scope="scope">
          <slot :row="scope.row" :index="scope.$index" :name="item.pram">
            <div>
              <el-image v-if="!(scope.row[item.pram])" :src="nopicture" style="width:75px;height:55px;" />
              <el-badge v-if="scope.row[item.pram]&&Array.isArray(scope.row[item.pram]) && scope.row[item.pram].length > 1" :value="Array.isArray(scope.row[item.pram]) && scope.row[item.pram].length > 1?scope.row[item.pram].length:0" class="item" :max="9">
                <el-image :src="scope.row[item.pram][0]" :preview-src-list="scope.row[item.pram]" style="width:75px;height:55px;display: flex;" />
              </el-badge>
              <el-image v-if="scope.row[item.pram]&&!(Array.isArray(scope.row[item.pram]))" :preview-src-list="[scope.row[item.pram]]" :src="scope.row[item.pram]" style="width:75px;height:55px;display: inline-table;" />
              <el-image v-if="scope.row[item.pram]&&(Array.isArray(scope.row[item.pram]))&&scope.row[item.pram].length===1" :src="scope.row[item.pram][0]" style="width:75px;height:55px;display: inline-table;" />
            </div>
          </slot>
        </template>
      </el-table-column>
      <!-- 按钮模块 -->
      <el-table-column v-else-if="item.btn" :key="index" :label="item.label" :width="item.width?item.width:'auto'" align="center" :render-header="renderHeader" :show-overflow-tooltip="!(JSON.stringify(item.tooltip)==='false')">
        <template slot-scope="scope">
          <div v-if="Array.isArray(item.pram)">
            <div v-for="btn of item.pram" :key="btn.id" style="display: inline-block;">
              <slot :row="scope.row" :index="scope.$index" :name="btn.btnlable">
                <div v-if="btn.isIcon === 'svgIcon' || btn.isIcon === 'icon'">
                  <el-link v-if="btn.isIcon === 'svgIcon'" style="font-size:18px;" :style="eval(scope.row,btn.disabled)?'':'color:#1890ff'" :underline="false" :disabled="eval(scope.row,btn.disabled)" @click.stop="handleButton(btn.btnType, scope.row, scope.$index)">
                    <svg-icon :style="eval(scope.row,btn.style)" :icon-class="btn.btnlable" />
                  </el-link>
                  <el-link v-else :underline="false" :disabled="eval(scope.row,btn.disabled)" :style="eval(scope.row,btn.disabled)?'':'color:#1890ff'" @click.stop="handleButton(btn.btnType, scope.row, scope.$index)">
                    <i style="font-size:18px;color:#1890ff" :style="eval(scope.row,btn.style)" :class="btn.btnlable" />
                  </el-link>
                </div>
                <el-link v-else :underline="false" type="primary" :disabled="eval(scope.row,btn.disabled)" :style="eval(scope.row,item.style)" @click.stop="handleButton(btn.btnType, scope.row, scope.$index)">
                  {{ btn.btnlable }}
                </el-link>
              </slot>
            </div>
          </div>
          <div v-else style="display: inline-block;">
            <slot :row="scope.row" :index="scope.$index" :name="item.pram">
              <div v-if="item.isIcon === 'svgIcon' || item.isIcon === 'icon'">
                <el-link v-if="item.isIcon === 'svgIcon'" style="font-size:18px;" :style="eval(scope.row,item.disabled)?'':'color:#1890ff'" :underline="false" :disabled="eval(scope.row,item.disabled)" @click.stop="handleButton(item.btnType, scope.row, scope.$index)">
                  <svg-icon :style="eval(scope.row,item.style)" :icon-class="item.pram" />
                </el-link>
                <el-link v-else :underline="false" :disabled="eval(scope.row,item.disabled)" :style="eval(scope.row,item.disabled)?'':'color:#1890ff'" @click.stop="handleButton(item.btnType, scope.row, scope.$index)">
                  <i style="font-size:18px;color:#1890ff" :style="eval(scope.row,item.style)" :class="item.pram" />
                </el-link>
              </div>
              <el-link v-else :underline="false" type="primary" :disabled="eval(scope.row,item.disabled)" :style="eval(scope.row,item.style)" @click.stop="handleButton(item.btnType, scope.row, scope.$index)">
                {{ item.pram }}
              </el-link>
            </slot>
          </div>
        </template>
      </el-table-column>
      <!-- 常规按钮 -->
      <el-table-column v-else-if="item.routineBtn" :key="index" :label="item.label" :render-header="renderHeader" class="specialItem" :width="item.width?item.width:'auto'" align="center" :show-overflow-tooltip="!(JSON.stringify(item.tooltip)==='false')">
        <template slot-scope="{row,$index}">
          <slot :row="row" :index="$index" :name="item.pram">
            <div v-if="Array.isArray(item.pram)" style="display:flex;justify-content: space-evenly;">
              <div v-for="(btn,i) of item.pram" :key="i" :class="eval(row,btn.class)">
                <el-button size="mini" :type="eval(row,btn.type)" plain :disabled="eval(row,btn.type)==='info'" class="routineBtn" @click="handleButton(btn.btnType, row, $index)">{{ eval(row,btn.btnlable) }}</el-button>
              </div>
            </div>
            <div v-else>
              <el-button size="mini" :type="eval(row,item.type)" plain :disabled="eval(row,item.type)==='info'" class="routineBtn" @click.stop="handleButton(item.btnType, row, $index)">{{ eval(row,item.pram) }}</el-button>
            </div>
          </slot>
        </template>
      </el-table-column>
      <!-- 过滤展示模块 -->
      <el-table-column v-else-if="item.funName" :key="index" :prop="item.pram" :render-header="renderHeader" :label="item.label" :width="item.width?item.width:'auto'" align="center" :show-overflow-tooltip="!(JSON.stringify(item.tooltip)==='false')">
        <template slot-scope="scope">
          <slot :row="scope.row" :index="scope.$index" :name="item.pram">
            {{ getFileter(item.funName, scope.row[item.pram]) }}
          </slot>
        </template>
      </el-table-column>
      <!-- 拼接展示 -->
      <el-table-column v-else-if="item.splice" :key="index" :prop="item.pram" :width="item.width?item.width:'auto'" :label="item.label" align="center" :show-overflow-tooltip="!(JSON.stringify(item.tooltip)==='false')">
        <template slot-scope="scope">
          <slot :row="scope.row" :index="scope.$index" :name="item.pram">
            <div v-if="item.pram !== '' && item.pram !== 'undefined'" class="textAlong" style="display:flex;justify-content: center;">
              <div v-for="(spac, z) in item.pram.split( item.spliceType && item.spliceType.length > 0 ? item.spliceType.split(',')[0] : '/' )" :key="z" style="margin-right:5px">
                <span v-if=" item.spliceType && item.spliceType.length > 2 && z > 0 ">{{ item.spliceType.substring(2) }}</span>
                <span v-if=" item.spliceType && item.spliceType.length <= 2 && z > 0 ">/</span>
                <span v-if="!item.spliceType && z > 0">/</span>
                {{ scope.row[spac] }}
              </div>
            </div>
          </slot>
        </template>
      </el-table-column>
      <!-- <el-table-column v-else-if="item.popover" :key="index" :prop="item.pram" min-width="150" :label="item.label" align="center" :show-overflow-tooltip="!(JSON.stringify(item.tooltip)==='false')">
          <template slot-scope="{row}">
            <el-popover placement="bottom-start" :title="item.label" width="240" trigger="hover">
              <hr style="color:#E8E8E8">
              <div v-for="item in row.workDownEq" :key="item" class="pop-item">
                {{ item }}
              </div>
              <div slot="reference" class="spillhide">{{ row.workDownEq.join('、') }}</div>
            </el-popover>
          </template>
        </el-table-column> -->
      <!-- 溢出提示、详情提示 -->
      <!-- <el-table-column v-else-if="item.tooltip" :key="index" :prop="item.pram" max-width="150" :label="item.label" align="center" :show-overflow-tooltip="!(JSON.stringify(item.tooltip)==='false')">
          <template slot-scope="scope">
            <el-popover placement="bottom-start" :title="item.label" width="240" trigger="hover">
              <slot slot="reference" :row="scope.row" :index="scope.$index" :name="item.pram">
              </slot>
            </el-popover>
          </template>
        </el-table-column> -->
      <!-- 默认展示模块 -->
      <el-table-column v-else-if="item.routineTable" :key="index" :prop="item.pram" :width="item.width?item.width:'auto'" :label="item.label" :render-header="renderHeader" align="center" :show-overflow-tooltip="!(JSON.stringify(item.tooltip)==='false')" />
      <el-table-column v-else :key="index" :prop="item.pram" :width="item.width?item.width:'auto'" :label="item.label" align="center" :render-header="renderHeader" :show-overflow-tooltip="!(JSON.stringify(item.tooltip)==='false')">
        <template slot-scope="{row,$index}">
          <slot :row="row" :index="$index" :name="item.pram">
            {{ row[item.pram] }}
          </slot>
        </template>
      </el-table-column>
    </template>
  </el-table>
  <!-- </div> -->
</template>

<script>
/**
 * 	 * list 列表页面
   * @description 列表组件，主要用于列表展示
   * @property {Array} mainData 列表数据
   * @property {Array} columns 列表title及绑定数据参数等
   * @property {String} columns[i].pram   绑定展示数据属性名
   * @property {String} columns[i].label  表头标题
   * @property {String} columns[i].funName  使用过滤器 并且过滤器的名称为columns[i].funName的值
   * @property {Boolean} columns[i].switch  是否是开关
   * @property {Boolean} columns[i].icon  是否是图标
   * @property {Boolean} columns[i].btn  是否是按钮
   * @property {Boolean} columns[i].btnType  绑定按钮类型 用于判断触发事件的按钮
   * @property {Boolean} columns[i].show  是否使用、分割数组
例：columns: [
  {pram: 'conId',label: '合同编号'},//正常数据
  {pram: 'listArr',label: '合同编号',show:true, subParam: 'name'},//绑定的listArr是个数组,subParam绑定数组对象的需要展示的页面
  {pram: 'complianceRate',label: '开关',switch:true},//使用开关
  {pram: 'svg图片名称',label: '履约率',icon:true},//使用图标
  {pram: 'specs/making',label: '规格/型号',splice: true,spliceType:"pram通过什么分割的,页面展示同什么符号分割"},//拼接展示默认
  { pram: 'nature', label: '生产性质', funName: "过滤方法方法名" },//过滤展示
  {pram: '按钮名称',label: '操作',btn:true,btnType:"edit"},//单列展示单个按钮
  {pram: [{
  : '按钮名称', btnType: 'edit' }, { btnlable: '按钮名称', btnType: 'edit' }],label: '操作',btn:true},//单列展示多个按钮
  {pram: '按钮图标名称',label: '操作',btn:true,btnType:"edit",isIcon: (svgIcon|icon)},//单列展示单个图标
  {pram: [{ btnlable: '按钮图标名称', btnType: 'edit' isIcon:  (svgIcon|icon)}, { btnlable: '按钮图标名称', btnType: 'edit', isIcon:  (svgIcon|icon) }],label: '操作',btn:true},//单列展示多个图标
    ]
   */
// import elTableInfiniteScroll from "el-table-infinite-scroll";
export default {
  name: "TableElement",
  directives: {
    // "el-table-infinite-scroll": elTableInfiniteScroll
  },
  props: {

    columns: { // 表头数据
      type: Array,
      default: function() {
        return [];
      }
    },
    mainData: { // 表格数据
      type: Array,
      default: function() {
        return [];
      }
    },
    showcheckbox: { // 判断是多选
      type: Boolean,
      default: true
    },
    showRadio: { // 判断是单选且有radio框
      type: Boolean,
      default: false
    },
    radioFlag: { // 单选时需要绑定一个唯一值  如果不传默认是id
      type: String,
      default: 'id'
    },
    selectRadio: { // 单选时需要绑定一个唯一值  如果不传默认是id
      type: Number,
      default: 0
    },
    showIndex: { // 是否显示序号
      type: Boolean,
      default: false
    },
    border: { // 缓存选中行数据
      type: Boolean,
      default: true
    },

  },
  data() {
    return {
      checkedRadio: this.selectRadio,
      nopicture: require('@/assets/image/nopicture.png'),
      isloading: false, // fasle  交易完成        true  交易进行中
      headerStyle: { background: "#FAFAFA", color: "rgba(0,0,0,0.85)" },
      // isshow: false

    };
  },
  computed: {

  },
  watch: {
    mainData: { // 监听请求是否完成
      handler(newValue, oldValue) {
        this.isloading = false;
        // this.isshow = true;
      }
    },
    selectRadio: { // 监听请求是否完成
      handler(newValue, oldValue) {
        this.checkedRadio = this.selectRadio
      }
    },
    deep: true,
    immediate: true // 关键
  },
  mounted() { },
  methods: {
    // 绑定行id
    getRowKey(row) {
      return row.id;
    },
    // 使用过滤器进行展示数据
    getFileter(funName, code) {
      return this.$options.filters[funName](code);
    },
    eval(row, condition) {
      if (row) {
        var pattern = new RegExp("[<>=+-]");
        // eslint-disable-next-line no-eval
        return eval(pattern.test(condition) ? condition : JSON.stringify(condition))
      }
    },
    // 开关方法
    setStatus(row, status) {
      this.$emit("setStatus", row, status);
    },
    // 操作按钮方法
    handleButton(btnType, row, index) {
      this.$emit("handleButton", btnType, row, index);
    },
    handleCurrentChange(row) {
      this.$emit("handleCurrentChange", row);
    },
    radioChange(row) {
      this.$emit('update:selectRadio', row[this.radioFlag])// 通过vue.async实现子组件修改父组件数据
      this.$emit("radioChange", row);
    },
    handleSelectionChange(row) {
      this.$emit("handleSelectionChange", row);
    },
    openDetail(row) {
      this.$emit("openDetail", row);
    },
    addTdClass(row) {
      if (row.column.property === "img") {
        return "img-column";
      }
    },
    radioEcho() {
      return this.selectRadio
    },
    filterHandler(value, row, column) {
      console.log(value, row, column)
      const property = column['property'];
      return row[property] === value;
    },
    search() {

    },
    selectable(row, index) {
      // if (index === 2) {
      return true
      // } else {
      //   return false
      // }
    },
    // 当表头超过四个字隐藏  防止换行 鼠标悬浮显示
    renderHeader(h, data) {
      if (data.column.label.length > 4) {
        return h("span", [
          h(
            "el-tooltip",
            {
              attrs: {
                class: "item",
                effect: "dark",
                content: data.column.label,
                placement: "top",
              },
            },
            [h("span", data.column.label)]
          ),
        ]);
      } else {
        return h("span", [
          [h("span", data.column.label)]
        ]);
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.textAlong {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.el-link {
  padding: 0px 5px;
}
::v-deep .el-table_3_column_21 .is-center {
  padding: 0;
}
.table-sty {
  height: 100%;
  //图片列样式
  ::v-deep .img-column {
    padding: 0;
    .cell {
      padding: 0;
      .item .el-badge__content {
        transform: translateY(0%) translateX(50%);
      }
    }
  }
  ::v-deep .el-table__body-wrapper {
    height: calc(100% - 60px) !important;
    overflow-y: auto;
  }
  ::v-deep ::-webkit-scrollbar {
    /*滚动条里面小方块*/
    width: 3px;
    height: 8px;
  }
  ::v-deep .cell {
    word-break: keep-all !important;
    white-space: nowrap !important;
    span span {
      display: block;
      text-overflow: ellipsis;
      white-space: nowrap;
      overflow: hidden;
    }
  }
  ::v-deep ::-webkit-scrollbar-thumb {
    /*滚动条里面小方块*/
    border-radius: 5px;
    // -webkit-box-shadow: inset 0 0 5px rgba(1, 21, 48, 1);
    // box-shadow: inset 0 0 5px rgba(1, 21, 48, 1);
    background: #c1c8ce;
  }
  ::v-deep ::-webkit-scrollbar-track {
    /*滚动条里面轨道*/
    // -webkit-box-shadow: inset 0 0 5px rgba(14, 54, 94, 0.2);
    // box-shadow: inset 0 0 5px rgba(14, 54, 94, 0.2);
    border-radius: 5px;
    background: #fff;
  }
}

.scrollbar-view {
  height: 100%;
  max-height: calc(100%);
  overflow-y: auto;
  ::v-deep .el-scrollbar__wrap .el-scrollbar__view {
    height: 100%;
  }
}
.specialItem {
  ::v-deep .el-button--success.is-plain {
    color: #26c2bc;
    background: #fff;
    border-color: #d3f4f2;
  }
  ::v-deep .el-button--info.is-plain {
    color: #939399;
    background: #fff;
    border-color: #d3d4d6;
  }
}
.routineBtn {
  background: #fff;
}
.clearradio {
  ::v-deep .el-radio__label {
    display: none;
  }
}
</style>
